import React, { Component } from 'react';
// import games from './Game/games.json';
import { Connect } from 'aws-amplify-react';
import { graphqlOperation } from 'aws-amplify';

//declare query to listGame
const ListGames = `query listGames{
  listGames(limit: 5){
    items{
      id
      Title
      Thumbnail
      Location
      Difficulty
      Story
      Questions
      QuestionVisualAid
      Answers
    }
  }
}`

class gameListLoader extends React.Component {
  render() {
    console.log("Home.js executed");
    return (
      <Connect query={graphqlOperation(ListGames)}>
        {({ data, loading, errors }) => {
          if (loading) { console.log("Loading...."); }
          if (errors) { console.log(errors); }
          console.log(data.ListGames)
        }}
      </Connect>
    )
  }
}

export default gameListLoader;